import React from 'react';
import styled from 'styled-components';
import { colorOptions } from '../theme';

const Wrapper = styled.span`
    display: inline-block;
    position: relative;
    color: ${textColor};

    &::before {
        content: '';
        position: absolute;
        height: 30%;
        width: 120%;
        background: ${backgroundColor};
        opacity: 0;
        bottom: -15%;
        left: -10%;
    }

    &:hover {
        &::before {
            opacity: 1;
        }
    }
`;

const HoverEffectText = ({children, textColor, backgroundColor }) => {
    return (
        <Wrapper>
            {children}
        </Wrapper>
    )
}

HoverEffectText.defaultProps = {
    textColor: `${colorOptions.red.PRIMARY_COLOR}`,
    backgroundColor: `${colorOptions.red.PRIMARY_COLOR_LIGHT}`
}

export default HoverEffectText;